body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header {
    font-size: x-large;
    background-image: -webkit-linear-gradient(left,#19387d, green);
    background-image: linear-gradient(to right,#19387d, green);
    color:white;
}
.gray {
    fill: gray !important;
}

.green {
    fill: green !important;
}

.title{
    width:40%;
}

.description {
    width: 50%;
} 

ul {
    list-style-type: disc;
    padding-bottom: 10px;
    
}

li {
    margin-bottom: 20px;
}
.container{
    display:flex;
    flex-direction: column;
    height:100vh;
    overflow: auto;
}

.header{
    flex:1 1;
    height: 30%;
    background-color: blue;
    display:flex;
    align-items: center;
    justify-content: center
}

.features{
    flex: 6 1;
    display: flex;
    flex-direction: column;
}

.search{
    flex:4 1;
}

.favorites {
    flex:4 1;
    background: rgba(51, 170, 51, 0.04);
}
.searchContainer {
    display:flex;
    flex-direction: column;
}
.searchFieldButton{
    align-items: center;
    justify-content: center;
    display: flex;
    flex-direction: row;
    padding: 10px;
}
.button{
    margin-left:5px;
    flex:1 1;
}

.searchfield {
    flex: 13 1;
}

